body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* SIDE BAR */

#site-sidebar {
    top: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    overflow: hidden;
    z-index: 2;
    width: 25%;
    font-size: 14px;
    color: #fff;
    text-align: left;
    background-color: rgb(127, 107, 153);
}

#site-sidebar-inner {
    position: relative;
    height: 100%;
    overflow-y: auto;
    padding: 45px 40px;
    text-align: center;
}

.site-sidebar .site-sidebar-inner {
    position: relative;
    height: 100%;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    padding: 45px 40px
}

#site-sidebar-inner-image {
    margin-top: 5px;
    margin-right: 55px;
    margin-bottom: 55px;
}

/* SIDE BAR INNER NAV */

#site-sidebar-inner-nav {
    margin-right: 75px;
    margin-bottom: 2px;
}

.main-navigation {
    margin: "45px -40px";
    line-height: "30px";
    position: "relative";
    font-size: "14px";
    text-transform: "uppercase";
    display: "block";
    text-align: "center";
}

.social-icons {
    float: left;
}

/* SideBar Contact */

#site-sidebar-inner-contact {
    margin-right: 25px
}

