@media screen and (max-width:1024px) {
    #site-sidebar {
        top: auto;
        bottom: auto;
        left: auto;
        right: auto;
        position: relative;
        width: 100%;
        padding-top: 40px;
        padding-bottom: 0
    }
    #site-sidebar:before {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: -50px;
        content: " ";
        display: block;
        width: auto;
        height: 8px;
    }
    #site-sidebar:before {
        display: none
    }
}

